.popup
    position: fixed
    padding: 0 0.5rem
    background-color: $popuplightcolor
    display: block
    border:  10px solid transparent
    border-radius: 15px
    z-index: 550

.popup-header
    user-select: none
    width: 100%
    font-size: 3rem
    border-bottom: 1px solid black
    padding-bottom: 0.5rem
    margin-bottom: 0.5rem
    max-height: 3.5rem
    overflow: hidden
    color: $popuplightheadercolor

.popup-body
    height:90%

.overlay
    left:0%
    top:0%
    position: fixed
    display: flex
    justify-content: center
    align-items: center
    background-color: $overlaycolor
    height: 100%
    width: 100%
    z-index: 500

@media screen and (max-width:768px) 
    .popup 
        width: 350px
    .popup input
        width: 140px
    .popup textarea
        width: 330px
    .popup button
        width: 150px
        font-size: 16px
