.welcome-page
    z-index: 10
    position: absolute
    width: 100%
    height: 100%
    display: flex
    justify-content: center
    align-items: center
    transition: opacity 2s
    background: transparent
    opacity: 1

.welcome-page-message
    user-select: none
    color: white
    font-size: 4rem
    transition: filter 2s
