.sheetboard
    transition: transform 0.2s
    overflow: hidden
    background-color: gainsboro

.sheetboard-object
    position: absolute
    cursor: initial

.sheetboard-map
    position: fixed
    transition: opacity 0.5s
    background-color: #0000ff80
    border-radius: 6px
    padding-right: 2px
    padding-bottom: 2px
    z-index: 150
    box-shadow: 0 0 5px 1px blue

.sheetboard-map-window
    z-index: 2
    cursor: pointer
    position: absolute
    background-color: #0000ffc7
    border: 1px #0000ffc7 solid
    border-radius: 6px
    box-shadow: 0 0 3px 1px
    transition: transform 0.2s
    overflow: hidden

.sheetboard-map-object
    position: absolute
    border-radius: 1.2px