.section-content
    position: relative
    left:120px
    height: 100%
    width: 90%
    background: transparent
    z-index: -1

.section-background
    width: 100%
    height: 100%
    position: absolute
    background-repeat: no-repeat
    background-size: cover
    top:0%
    z-index: -100

.section-background video
    object-fit: cover
    position: relative
    height: 100%
    z-index: -1000

.section-content-card
    z-index: 5
    position: relative
    color: white
    font-size: 5rem 
    top:35%
    width: fit-content
    height:300px
    padding: 2rem

.section-content-card2
    z-index: 5
    position: relative
    color: white
    font-size: 3rem 
    top:20%
    width: 70%
    padding: 3rem 2rem

.section-content-image-box
    position: absolute

.section-content-image-box img
    z-index: 1
    width: 300px
    height: 300px
    transition: all 0.7s

.section-content-image-box:hover
    z-index: 6

.section-content-image-box img:hover
    margin-top: -50px
    margin-left: -50px
    width:400px
    height: 400px

@media screen and (min-width:0px) and (max-width: 500px) 
    .section-content
        left:0
        width: 100%
        
    .section-content-card
        font-size: 3rem 
    
    .section-content-card2
        position: relative
        color: white
        font-size: 1.5rem 
        top:20%
        width: 100%
