.about-author-window
    font-size: 1.4rem
    padding: 2rem

.app-window
    width: calc(100% - 24px)
    height: calc(100% - 24px)
    padding: 12px
    display: flex
    flex-direction: column
    align-items: center
    color: white

.app-window h1
    margin-block-start: 0
    margin-block-end: 0
    margin-bottom: 15px

.app-window iframe
    width:100%
    height: 100%
    border: none

.scrolling-app-window iframe
    margin-bottom: 15px
    background: black
    

.app-window .iphone-button
    border-radius: 100%
    width: 52px
    height: 52px
    background-color: white
    transition: background 0.5s, border 1s
    box-shadow: 0px 0px 0.7px 1.5px #00000047

.app-window .iphone-button:active
    background-color: grey
    border-color: white
    border-style: none